.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  border-bottom: 1px solid lightgray;
  padding: 0.8em 2em;
  background-color: var(--main-color);
  color: var(--main-text-color);
  position: sticky;
  top: 0;
}

.container_homepage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  border-bottom: 1px solid lightgray;
  padding: 0.8em 2em;
  background-color: var(--main-color);
  color: var(--main-text-color);
  position: sticky;
  top: 0;
  animation: colorChange 3s infinite alternate;
}

@keyframes colorChange {
  from {
    background-color: var(--main-color);
  }
  to {
    background-color: var(--main-toggle-color);
  }
}

.title {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  font-weight: 700;
  font-style: italic;
  cursor: pointer;
  text-decoration: none;
  color: var(--main-text-color);
}

.title:hover {
  color: var(--main-dark-color);
}

.title_homepage {
  display: flex;
  align-items: center;
  font-size: 1.5em;
  font-weight: 700;
  font-style: italic;
  cursor: pointer;
  text-decoration: none;
  color: var(--main-text-color);
}

.title_homepage:hover {
  color: var(--main-dark-color);
}

.logo {
  font-size: 1.2em;
}

.text {
  padding-left: 0.3em;
}

.login {
  display: flex;
  align-items: center;
}

.store {
  border: 1px solid var(--main-text-color);
  border-radius: 8px;
  padding: 0.5em 1em;
  font-size: 0.8rem;
  cursor: pointer;
}

.store:hover {
  background-color: var(--main-dark-color);
  font-size: 0.81rem;
}

.icon {
  font-size: 1.5rem;
  padding-left: 1.5em;
  cursor: pointer;
}

.icon:hover {
  color: var(--main-dark-color);
}

.button {
  border: 1px solid var(--main-text-color);
  border-radius: 20px;
  padding: 0.5em 2.3em;
  font-size: 1rem;
  cursor: pointer;
}

.button:hover {
  background-color: var(--main-dark-color);
}
